<template>
  <div v-theme="'wide'" id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label for="">博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxs">
        <label for="">vue.js</label>
        <input type="checkbox" value="vue.js" v-model="blog.catagories">
        <label for="">node.js</label>
        <input type="checkbox" value="node.js" v-model="blog.catagories">
        <label for="">angular.js</label>
        <input type="checkbox" value="angular.js" v-model="blog.catagories">
        <label for="">react.js</label>
        <input type="checkbox" value="react.js" v-model="blog.catagories">
      </div>
      <label for="">作者：</label>
      <select v-model="blog.author">
        <option v-for="(author,index) in authors" :key="index">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-if="submmited">
      <h3>您的博客发布成功！</h3>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(category, index) in blog.catagories" :key="index">
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  //http://jsonplaceholder.typicode.com/
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:'',
        content:'',
        catagories:[],
        author:''
        
      },
      authors:['ll','haha','mary'],
      submmited:false
    }
  },
  methods:{
    post:function() {
      this.$http.post("http://jsonplaceholder.typicode.com/posts",{
        title:this.blog.title,
        body:this.blog.content,
        userId:2
      })
      .then(function(data){
          console.log(data);
          this.submmited=true;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* scoped仅限于当前页面使用 */
#add-blog *{
  box-sizing: border-box;
}
input{
  outline: 0;
  border: none;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  font-size: 20px;
  background: #6677cc;
}
label{
  display: block;
  margin: 20px 0 10px;
}
textarea{
  height: 200px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
#checkboxs label{
  display: inline-block;
  font-size: 18px;
}
#checkboxs input{
  display:inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border: 1px dotted grey;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



