<template>
    <nav>
        <ul>
            <li>
                <router-link to='/' exact>博客浏览</router-link>
                <router-link to='/add' exact>写博客</router-link>
                <router-link to='/es' exact>es6</router-link>
            </li>
        </ul>
    </nav>
</template>
<script>
export default {
    name:'blog-header',
}
</script>

<style scoped>
ul,li{
    list-style: none;
}
nav{
    text-align: center;
    margin-bottom: 20px;
    background:crimson ;
    line-height: 50px;
}
li{
    display: inline-block;
    padding:20px;
    color: #333;
    font-size: 20px;
}
a{
    text-decoration: none;
    border-radius: 5px;
    padding: 12px;
    color: #fff;
}
.router-link-active{
    background: rgba(255, 255,255, .8);
    color: #333;
}
</style>


