<template>
    <div id="es-demo">
       <div class="one">
            <p>1、let只在命令所在的代码块内有效</p>
            <p>2、变量一定要在声明之后使用，否则就报错</p>
            <p>3、let不允许在相同作用域内，重复声明同一个变量。</p>
       </div>
       <div class="one">
           <p></p>
           <p></p>
           <p></p>
       </div>
    </div>
</template>
<script>
export default {
    name:'es-demo',
    data(){
        return{

        }
    }
}
{
    let a = 10;//let只在命令所在的代码块内有效
    var b = 1;    
}

</script>
<style scoped>
.one{
    border: 1px solid #e5e5e5;
    padding: 20px;
    margin: 20px;
}
</style>


