<template>
  <div v-theme:column="'wide'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="请输入搜索内容">
    <div class="single-blog">
        <ul>
            <li v-for="(blog,index) in filterBlogs" :key="index">
               <router-link :to="'/blog/' + blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link> 
                <p>{{blog.body | snaap}}</p>
            </li>
        </ul>
    </div>
  </div>
</template>
<style scoped>
#show-blogs{
    margin: 0 auto;
}
ul,li{
    list-style: none;
}
input{
    border: 1px solid #e5e5e5;
    line-height: 40px;
    font-size: 16px;
    color: #666;
    outline: 0;
}

ul{
    padding: 20px;
}
li{
    border: 1px solid #e5e5e5;
    margin-bottom: 20px;
    padding: 20px;
    background: #e5e5e5;
}
a{
    text-decoration: none;
}
</style>
<script>

export default {
  name: 'show-blogs',
    data(){
        return{
            blogs:[],
            search:''
        }
    },
    created(){
        this.$http.get("http://jsonplaceholder.typicode.com/posts")
      // this.$http.get("../../static/posts.json") // 本地数据请求，只能放在static里面
        .then(function(data){
           // console.log(data);
            this.blogs = data.body.slice(0,20);
            // console.log(this.blogs);
        })
    },
    computed:{
        filterBlogs:function(){
            return this.blogs.filter((blog)=>{
                return blog.title.match(this.search);
            })
        }
    },
    filters:{
        // "to-uppercase":function(value){
        //     return value.toUpperCase();
        // }
        toUppercase(value){
            return value.toUpperCase();
        }
    },
    directives: {
        "rainbow":{
            bind(el,binding,vnode){
                el.style.color = "#" + Math.random().toString(16).slice(2,8)
            }
        }
    }
}

</script>





